
import { Modal } from "vue-neat-modal";
import 'vue-neat-modal/dist/vue-neat-modal.css'
import { ref} from 'vue';
export default {
    name : 'PsImageModal',
    components : {
        Modal,
    },
    props : {       
        maxWidth : {
            type : String,
            default : "100%"
        },
        isBackdropRemoved : {
            type : Boolean,
            default : false
        },
        isMotionDisabled : {
            type : Boolean,
            default : false
        }, 
        isClickOut : {
            type : Boolean,
            default : true
        }, 
        modalTransition : {
            type : String,
            default : "scale"
        }, 
        isFullscreen : {
            type : Boolean,
            default : false
        }
    },
    setup() {
        const isOpen = ref(false);
        
        function toggle(status) {
            isOpen.value = status;
        }
       
        return {
            isOpen,
            toggle,
        }
    }
}
